.icon {
  width: 24px;
  position: relative;
  height: 24px;
  object-fit: cover;
}
.brandname1 {
  position: relative;
  line-height: 150%;
  font-weight: 600;
}
.brand1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 var(--padding-5xs);
  z-index: 0;
}
.cta2 {
  position: relative;
  font-size: var(--font-size-sm);
  line-height: 160%;
  font-weight: 600;
  font-family: var(--font-inter);
  color: var(--color-whitesmoke-100);
  text-align: center;
}
.nav-links,
.text-container2 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0 var(--padding-9xs);
}
.nav-links {
  cursor: pointer;
  border: 1px solid var(--color-whitesmoke-100);
  padding: var(--padding-xs) var(--padding-xl);
  background-color: transparent;
  border-radius: var(--br-9xs);
  box-sizing: border-box;
  height: 48px;
  z-index: 1;
}
.nav-links:hover {
  background-color: var(--color-dimgray-100);
}
.nav-label3 {
  position: relative;
  line-height: 160%;
  font-weight: 600;
}
.nav-link,
.nav-links1,
.typecentered-nav {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.nav-link {
  align-self: stretch;
  justify-content: flex-start;
  padding: 0 var(--padding-base);
}
.nav-links1,
.typecentered-nav {
  margin: 0 !important;
  position: absolute;
  box-sizing: border-box;
}
.nav-links1 {
  top: calc(50% - 18.5px);
  left: calc(50% - 137.2px);
  height: 38px;
  justify-content: flex-start;
  padding: 0 var(--padding-base);
  gap: var(--gap-base);
  z-index: 2;
  font-size: var(--font-size-sm);
}
.typecentered-nav {
  width: 100%;
  top: -0.5px;
  right: 0;
  left: 0;
  background-color: var(--color-gray-100);
  height: 83px;
  justify-content: space-between;
  padding: var(--padding-5xs);
  z-index: 5;
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--color-whitesmoke-100);
  font-family: var(--font-inter);
}
